// @flow

import React, {
  useState,
} from 'react';
import {
  useDispatch,
} from 'react-redux';
import { createUser } from '../../widgets/User';
import backgroundLeft from '../../images/background_left.png';
import backgroundRight from '../../images/background_right.png';
import logo from '../../images/logo.png';
import './index.scss';

const DEV_USE = {
  name: 'shawn',
  age: 25,
};

function UserDataPage() {
  const [name, setName] = useState(process.env.NODE_ENV === 'development' ? DEV_USE.name : '');
  const [age, setAge] = useState(process.env.NODE_ENV === 'development' ? DEV_USE.age : '');
  const [gender, setGender] = useState('male');

  const dispatch = useDispatch();

  return (
    <div className="landing-page">
      <img src={backgroundLeft} alt="" />
      <img src={backgroundRight} alt="" />
      <div className="input-row-wrapper">
        <img className="logo" src={logo} alt="" />
        <div className="input-row">
          <span className="label">name:</span>
          <input
            type="text"
            value={name}
            onChange={e => setName(e.target.value)}
            placeholder="name" />
        </div>
        <div className="input-row">
          <span className="label">age:</span>
          <input
            type="text"
            value={age}
            onChange={e => setAge(e.target.value.replace(/[^0-9]/, ''))}
            placeholder="age" />
        </div>
        <div className="input-row">
          <span className="label">gender:</span>
          <select
            value={gender}
            onChange={({ target }) => setGender(target.value)}>
            {['male', 'female'].map(option => (
              <option
                key={option}
                value={option}>
                {option}
              </option>
            ))}
          </select>
        </div>
        <button
          type="button"
          onClick={() => dispatch(createUser({
            name,
            age,
            gender,
          }))}>
          Send
        </button>
      </div>
    </div>
  );
}

UserDataPage.defaultProps = {

};

export default UserDataPage;
