import { API_REQUEST } from 'redux-middleware-fetch';
import { getTestSetData } from './Result';

const TEST_SET_ID = 1;

const CREATE_USER = 'CREATE_USER';

export default function reducer(state = {
  userId: null,
}, action = {}) {
  switch (action.type) {
    case CREATE_USER:
      return ({
        ...state,
        userId: action.user.id,
      });

    default: return state;
  }
}

export function createUser(data) {
  const {
    name,
    gender,
    age,
  } = data;

  return dispatch => dispatch({
    [API_REQUEST]: {
      types: [
        CREATE_USER,
      ],
      auth: true,
      method: 'POST',
      body: {
        name,
        gender,
        age,
      },
      json: true,
      entrypoint: '/user',
      onSuccess: () => {
        dispatch(getTestSetData(TEST_SET_ID));
      },
    },
  });
}