import { push } from 'connected-react-router';
import { API_REQUEST } from 'redux-middleware-fetch';
import randomSort from '../helpers/randomSort';
import findImageById from '../helpers/findImageById';

const GET_TEST_SET = 'GET_TEST_SET';
const SEND_SET_RESULT = 'SEND_SET_RESULT';
const SEND_FINAL_RESULT = 'SEND_FINAL_RESULT';

export default function reducer(
  state = {
    imageSets: [],
    resultImageSet: {
      id: 'final',
      name: 'final',
      images: [],
    },
    finalResult: {},
  },
  action = {},
) {
  switch (action.type) {
    case GET_TEST_SET:
      return ({
        ...state,
        imageSets: action.testSet.imageSets.map(s => ({
          ...s,
          id: s.id,
          images: randomSort(s.images),
        })),
      });

    case SEND_SET_RESULT: {
      const selectedImage = findImageById(state.imageSets, action.imageId);

      return ({
        ...state,
        redirectTo: `/set/${action.nextImageSetId === -1 ? 'final' : action.nextImageSetId}`,
        resultImageSet: {
          ...state.resultImageSet,
          images: [
            ...state.resultImageSet.images,
            selectedImage,
          ],
        },
      });
    }

    case SEND_FINAL_RESULT: {
      const selectedImage = findImageById(state.imageSets, action.imageId);

      return ({
        ...state,
        redirectTo: `/completed`,
        finalResult: selectedImage,
      });
    }

    default:
      return state;
  }
};

export function getTestSetData(testSetId) {
  return dispatch => dispatch({
    [API_REQUEST]: {
      types: [
        GET_TEST_SET,
      ],
      auth: true,
      method: 'GET',
      entrypoint: `/testSet/${testSetId}`,
      onSuccess: ({ testSet }) => {
        const firstImageSetId = testSet.imageSets[0].id;
        dispatch(push(`/set/${firstImageSetId}`));
      },
    },
  });
}

export function sendSetResult(userId, setId, imageId) {
  return (dispatch, getState) => dispatch({
    [API_REQUEST]: {
      types: [
        SEND_SET_RESULT,
      ],
      auth: true,
      method: 'POST',
      body: {
        userId,
        imageId,
      },
      json: true,
      entrypoint: '/setResult',
      dispatchPayload: {
        imageId,
      },
      onSuccess: () => {
        console.log('AAA');
        const { Result: { imageSets } } = getState();

        const imageSetIndex = imageSets.findIndex(set => `${set.id}` === setId);

        if (imageSetIndex + 1 >= imageSets.length) {
          dispatch(push('/set/final'));
        } else {
          const newImageSetId = imageSets[imageSetIndex + 1].id;
          dispatch(push(`/set/${newImageSetId}`));
        }
      },
    },
  });
}

export function sendFinalResult(userId, imageId) {
  return dispatch => dispatch({
    [API_REQUEST]: {
      types: [
        SEND_FINAL_RESULT,
      ],
      auth: true,
      method: 'POST',
      body: {
        userId,
        imageId,
      },
      json: true,
      entrypoint: '/finalResult',
      dispatchPayload: {
        imageId,
      },
      onSuccess: () => {
        dispatch(push('/completed'));
      }
    },
  });
}