import React, {
  useEffect,
} from 'react';
import { Route, Switch } from 'react-router-dom';
import ImageSetReadyPage from './ImageSetReadyPage';
import ImageSetChoosePage from './ImageSetChoosePage';
import {
  useDispatch,
  useSelector,
} from 'react-redux';
import { push } from 'connected-react-router';

function ImageSetSwitch() {
  const dispatch = useDispatch();
  const userId = useSelector(state => state.User.userId);

  useEffect(() => {
    if (!userId) {
      dispatch(push('/'));
    }
  }, [dispatch, userId]);

  return (
    <Switch>
      <Route path="/set/:setId/start">
        <ImageSetChoosePage />
      </Route>
      <Route path="/set/:setId">
        <ImageSetReadyPage />
      </Route>
    </Switch>
  );
}

export default ImageSetSwitch;
