import React, {
  useMemo,
  useEffect,
} from 'react';
import {
  useHistory,
  useParams,
  useLocation,
} from 'react-router-dom';
import {
  useSelector,
} from 'react-redux';
import './index.scss';

function ImageSetReadyPage() {
  const location = useLocation();
  const history = useHistory();
  const { setId } = useParams();

  const isFinal = useMemo(() => setId === 'final', [setId]);

  const imageSetData = useSelector(state => {
    if (isFinal) {
      return state.Result.resultImageSet;
    }

    const imageSets = state.Result.imageSets;
    return (
      imageSets.find(set => `${set.id}` === setId) || {}
    )
  });

  const userId = useSelector(state => state.User.userId);

  useEffect(() => {
    if (!userId) {
      history.push('/');
    }
  }, [userId, history]);

  return (
    <div className="image-set-ready-page">
      {imageSetData.id === 'final' ? (
        <span>Final Round</span>
      ) : (
        <span>
          type:
          {imageSetData.name || 'ERROR'}
        </span>
      )}
      <button
        type='button'
        onClick={() => history.push(`${location.pathname}/start`)}>
        START
      </button>
    </div>
  );
}

export default ImageSetReadyPage;
