// @flow

import React, {
  useMemo,
  useState,
  useCallback,
  useEffect,
} from 'react';
import {
  useParams,
} from 'react-router-dom';
import {
  useSelector,
  useDispatch,
} from 'react-redux';
import './index.scss';
import { sendFinalResult, sendSetResult } from '../../../widgets/Result';

function ImageSetChoosePage() {
  const { setId } = useParams();
  const dispatch = useDispatch();

  const isFinal = setId === 'final';

  const imageSetData = useSelector(state => {
    if (isFinal) {
      return state.Result.resultImageSet;
    }

    const imageSets = state.Result.imageSets;
    return (
      imageSets.find(set => `${set.id}` === setId) || {}
    )
  });

  const [loading, setLoading] = useState(true);

  const originalImages = useMemo(() => imageSetData.images || [], [imageSetData.images]);
  const setName = useMemo(() => {
    if (imageSetData.id === 'final') return '總決賽';

    return imageSetData.name;
  }, [imageSetData.id, imageSetData.name]);

  const cacheImgs = useCallback(async (srcArray) => {
    const promises = await srcArray.map((src) => {
      return new Promise((resolve, reject) => {
        const img = new Image();

        img.src = src;
        img.onload = resolve();
        img.onerror = reject();
      });
    });

    await Promise.all(promises);

    setLoading(false);
  }, []);

  useEffect(() => {
    cacheImgs(originalImages);
  }, [cacheImgs, originalImages]);

  const [counter, setCounter] = useState(1);
  const [leftPicIndex, setLeftPicIndex] = useState(0);
  const [rightPicIndex, setRightPicIndex] = useState(1);
  const [leftPicked, setLeftPicked] = useState(false);
  const [rightPicked, setRightPicked] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const userId = useSelector(state => state.User.userId);

  const sendResult = useCallback((resultImageId) => {
    if (isFinal) {
      dispatch(sendFinalResult(userId, resultImageId))
    } else {
      dispatch(sendSetResult(userId, setId, resultImageId))
    }
  }, [dispatch, isFinal, setId, userId])

  const pickLeft = useCallback(() => {
    if (disabled) return;

    if (counter === originalImages.length - 1) {
      const resultImage = originalImages[leftPicIndex];

      sendResult(resultImage.id);
    } else {
      setDisabled(true);
      setLeftPicked(true);

      setTimeout(() => {
        setRightPicIndex(counter + 1);
        setCounter(counter + 1);

        setTimeout(() => {
          setLeftPicked(false);

          setTimeout(() => {
            setDisabled(false);
          }, 200)
        }, 200)
      }, 200);
    }
  }, [disabled, counter, originalImages, leftPicIndex, sendResult]);

  const pickRight = useCallback(() => {
    if (disabled) return;

    if (counter === originalImages.length - 1) {
      const resultImage = originalImages[rightPicIndex];

      sendResult(resultImage.id);
    } else {
      setDisabled(true);
      setRightPicked(true);
      setTimeout(() => {
        setLeftPicIndex(counter + 1);
        setCounter(counter + 1);

        setTimeout(() => {
          setRightPicked(false);

          setTimeout(() => {
            setDisabled(false);
          }, 200)
        }, 200)
      }, 200)
    }
  }, [counter, disabled, originalImages, rightPicIndex, sendResult]);

  if (loading) return null;

  return (
    <div className="image-set-choose-page">
      <div className="header">
        <span>{`${setName}: ${counter}/${originalImages.length - 1}`}</span>
      </div>
      <div className="click-block-wrapper">
        <div className="click-block-expander">
          <div
            className="click-block"
            onClick={pickLeft}
            style={{
              backgroundImage: `url(${originalImages[leftPicIndex]?.url})`,
              opacity: rightPicked ? 0 : 1,
            }} />
        </div>
      </div>
      <div className="click-block-wrapper right">
        <div className="click-block-expander">
          <div
            className="click-block"
            onClick={pickRight}
            style={{
              backgroundImage: `url(${originalImages[rightPicIndex]?.url})`,
              opacity: leftPicked ? 0 : 1,
            }} />
        </div>
      </div>
    </div>
  );
}

ImageSetChoosePage.defaultProps = {

};

export default ImageSetChoosePage;
